.App {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  background-color: rgb(24, 24, 24);
  color: #ff3333;
  font-size: 24px;
}

#radio-dial {
  width: 200px;
  height: 200px;
  display: flex;
  justify-content: center;
  border-radius: 50%;
  background-color: rgb(24, 24, 24);
  border: 2px solid white;
}

#radio-dial-tab {
  height: 20px;
  width: 2px;
  background-color: white;
}

#radio-bar {
  width: 500px;
  height: 100px;
  position: relative;
  border: 1px solid white;
  display: flex;
  align-items: center;
  justify-content: center;
}

#radio-bar-tab {
  position: absolute;
  height: 100%;
  width: 2px;
  background-color: white;
}

.radio-bar-stop {
  position: absolute;
  height: 50%;
  top: 0;
  width: 1px;
  background-color: white;
}

#radio-bar-stop-1 {
  left: 25%;
}

#radio-bar-stop-2 {
  left: 50%;
}

#radio-bar-stop-3 {
  left: 75%;
}

#podcast {
  position: absolute;
  top: calc(100% + 1px);
}



#popup {
  position: absolute;
  z-index: 100;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  
}

.playButton {
  display: none;
}

#popup-background {
  position: absolute;
  display: inline-block;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
}

#popup-message {
  display: flex;
  flex-direction: column;
  background-color: rgb(24, 24, 24);
  border-radius: 5%;
  width: 600px;
  z-index: 1000;
  color: #e1e1e1;
  padding: 30px;
  font-size: 19px;
}